*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    overflow: hidden;
}

.mainwindow-start,.mainwindow-end{
    width: 100vw;
    height: 100vh;
    background-color: #121212;
    opacity: 1;
}
.mainwindow-end{
    animation: screenfadeout 0.5s ease-in-out 0s 1 normal forwards;
}
@keyframes screenfadeout {
    0%{
        opacity: 1;
    }
    100%{
        opacity: 0;
    }
}

.logoholder{
    position: absolute;
    top: 45%;
    left: 50%;
    transform: translate(-50%,-50%);
    width: 220px;
    height: 220px;
    display: flex;
    justify-content: center;
    align-items: center;
    /* background-color: white; */
    opacity: 0;
    animation: logofade 0.5s ease-in-out 1s 1 normal forwards;
}
@keyframes logofade {
    0%{
        opacity: 0;
    }
    100%{
        opacity: 1;
    }
}
.logoholder img{
    width: 180px;
    height: auto;
    border-radius: 50%;
    box-shadow: 10px 10px 10px black;
}

.openingloader{
    position: absolute;
    top: 70%;
    left: 50%;
    transform: translate(-50%,-50%);
    width: 200px;
    height: 80px;
    /* background-color: aliceblue; */
    display: flex;
    justify-content: center;
    align-items: center;
}
.firstcircle,.secondcircle,.thirdcircle,.fourthcircle{
    width: 10px;
    height: 10px;
    border-radius: 50%;
    margin: 8px;
    background-color: rgba(255,255,255,0.5);
}
.firstcircle{
    animation: loader 0.5s ease-in-out 0s infinite normal forwards;
}
.secondcircle{
    animation: loader 1s ease-in-out 0.25s infinite normal forwards;
}
.thirdcircle{
    animation: loader 1s ease-in-out 0.75s infinite normal forwards;
}
.fourthcircle{
    animation: loader 1s ease-in-out 1s infinite normal forwards;
}
@keyframes loader {
    0%{
        background-color:rgba(255,255,255,0.5);
    }
    50%{
        background-color: white;
    }
    100%{
        background-color: rgba(255,255,255,0.5);
    }
}
.openingloader.hidden{
    display: none;
}


.loginscreen-in,.loginscreen-out{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    width: 500px;
    /* height: 630px; */
    height: 90%;
    /* z-index: 10; */
    background-color: black;
    border-radius: 10px;
}
.loginscreen-in{
    opacity: 0;
    animation: fadein 0.5s ease-in-out 0s 1 normal forwards;
}
.loginscreen-out{
    opacity: 1;
    animation: fadeout 0.5s ease-in-out 0s 1 normal forwards;
}

.signupscreen-in,.signupscreen-out{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
